@import "../colors/colors.scss";

.header {
  height: 8vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 5vw;
  font-size: 1.5rem;
  font-weight: 700;
  a {
    color: $black;
    text-decoration: none;
    font-family: "Syne", sans-serif;
