.clicker {
  font-family: "Fira Code", monospace;
  height: 82vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}
.clicker input {
  border: none;
  background-color: transparent;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}
.clicker input:hover {
  transform: scale(1.1);
}
.clicker input:active {
  transform: scale(0.9);
}
.clicker .upgrade {
  border: #aa744c 0.5rem solid;
  border-radius: 1rem;
  text-align: center;
  padding: 1rem;
  height: 12rem;
  width: 10rem;
  background-color: #fcb860;
}
.clicker .upgrade span {
  font-size: 0.75rem;
  font-weight: 500;
}
.clicker .upgrade button {
  margin: 10px;
  padding: 10px;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  background-color: #b38051;
  color: #fff;
  cursor: pointer;
  box-shadow: 5px 5px 0px #aa744c;
  border-radius: 14px;
}
.clicker .upgrade button:hover {
  background-color: #aa744c;
  translate: -2px -2px;
  box-shadow: 7px 7px 2px #aa744c;
}
.clicker .upgrade p {
  font-size: 1rem;
  font-weight: 700;
}
.clicker button {
  margin: 10px;
  padding: 10px;
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  background-color: #222;
  color: #fff;
  cursor: pointer;
  box-shadow: 5px 5px 0px #222;
  border-radius: 14px;
}
.clicker button:hover {
  translate: -2px -2px;
  box-shadow: 7px 7px 2px #222;
}/*# sourceMappingURL=clicker.css.map */